.container {
    display: -ms-flexbox;
    display: flex;
    padding: 0.5em;
}

.container2 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.pokemonImageContainer {
    width: 5em;
    height: 5em;
    padding: 0.5em;
}

.pokemonImageContainer img {
    max-width:100%;
    max-height:100%;
}

.iconContainer span {
    display: inline-block;
    text-align: center;
    width: 1.5em;
}

.iconContainer img {
    width: 1.2em;
}

.active {
    color: forestgreen;
}

.expired {
    color: red;
}


